<h2>Attributes</h2>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>title</td>
<td>聊天框标题</td>
<td>String</td>
<td>—</td>
<td>-</td>
</tr>
<tr>
<td>messages</td>
<td>消息内容数组</td>
<td>Array</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>footer</td>
<td>发送框</td>
<td>slot</td>
<td>-</td>
<td>-</td>
</tr>
</tbody>
</table>
<h2>Messages</h2>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>id</td>
<td>消息唯一标识</td>
<td>String,Number</td>
<td>—</td>
<td>-</td>
</tr>
<tr>
<td>type</td>
<td>消息内容类型</td>
<td>String</td>
<td>text,system,自定义标签（组件名）,renderEl</td>
<td>-</td>
</tr>
<tr>
<td>user</td>
<td>用户信息</td>
<td>String</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>time</td>
<td>发送时间</td>
<td>String</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>content</td>
<td>组件内容需要属性</td>
<td>Object</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>position</td>
<td>left right</td>
<td>发送者位置</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>renderEl</td>
<td>自定义渲染组件</td>
<td>Function</td>
<td>-</td>
<td>-</td>
</tr>
</tbody>
</table>
<h2>User</h2>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>avatar</td>
<td>用户头像链接</td>
<td>String</td>
<td>—</td>
<td>-</td>
</tr>
</tbody>
</table>
</section></template>

<script>
export default {
  data() {
    return {
        messages:
           [
                   {
                     id :1,
                     type: 'text',
                     content: { text: '主人好，我是智能助理，你的贴心小助手~' },
                     user: { avatar: '//gw.alicdn.com/tfs/TB1DYHLwMHqK1RjSZFEXXcGMXXa-56-62.svg' },
                     time:"2020/04/24 17:52:12"
                   },
                   {
                     type: 'system',
                     id :2,
                     content: {
                       src: 'https://fuss10.elemecdn.com/d/e6/c4d93a3805b3ce3f323f7974e6f78jpeg.jpeg',
                       type: 'card',
                       title: '测试',
                       desc: '尊敬的客户：感谢您参加流量大放送活动，恭喜您获得' ,
                       plain: true,
                       name:"哈哈哈哈哈",
                       id:"1545454",
                       buttons:[{
                             name: '标签一',
                             color: 'primary'
                           }, {
                             name: '标sdsdsddgsgs签四',
                             color: 'success'
                           }, {
                             name: '标sdsdsddgsgs签四',
                             color: 'info'
                           }
                                  ]
                     },
                     user: { avatar: '//gw.alicdn.com/tfs/TB1DYHLwMHqK1RjSZFEXXcGMXXa-56-62.svg' },
                     position: 'right',
                     time:"2020/04/24 17:52:12"
                   },
                   {
                     id :3,
                     type: 'system',
                     renderEl : (h, params)=>{
                           params.style={width: '224px', height: '144px' }
                           params.attrs={title:'123',src: 'https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg',}
                           return h('xm-img',params)
                      },
                     user: { avatar: '//gw.alicdn.com/tfs/TB1DYHLwMHqK1RjSZFEXXcGMXXa-56-62.svg' }
                   },
                   {
                     id :4,
                     type: 'xm-audio',
                     content: {
                       width:224,
                       height:144,
                       src:"http://www.170mv.com/kw/other.web.rj01.sycdn.kuwo.cn/resource/n3/2/63/3890495760.mp3"
                       },
                       user: { avatar: '//gw.alicdn.com/tfs/TB1DYHLwMHqK1RjSZFEXXcGMXXa-56-62.svg' },
                       position: 'right'
                   }
                 ]
     }
  },
}
</script>


