                <span class="hljs-tag">&lt;<span class="hljs-name">xm-media</span> <span class="hljs-attr">src</span>=<span class="hljs-string">"https://cms.cnc.blzstatic.cn/cms/gallery/4G8KGRWWG7FS1557732437703.mp4"</span> <span class="hljs-attr">poster</span>=<span class="hljs-string">"https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg"</span>&gt;</span>
