</code></pre>
</div></demo-block>
<h2>API</h2>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>title</td>
<td>卡片主题名称</td>
<td>String</td>
<td>—</td>
<td>-</td>
</tr>
<tr>
<td>actions</td>
<td>按钮方法</td>
<td>Array</td>
<td>—</td>
<td>-</td>
</tr>
<tr>
<td>key  与action 选其一</td>
<td>按钮key触发emit</td>
<td>String</td>
<td>—</td>
<td>-</td>
</tr>
<tr>
<td>src</td>
<td>按钮图标链接</td>
<td>String</td>
<td>—</td>
<td>-</td>
</tr>
<tr>
<td>menus</td>
<td>按钮列表</td>
<td>String</td>
<td>—</td>
<td>-</td>
</tr>
<tr>
<td>menus的其余</td>
<td>buttonMore组件有详细说明</td>
<td>String</td>
<td>—</td>
<td>-</td>
</tr>
</tbody>
</table>
</section></template>

<script>
export default {
  data() {
    return {
        menus: [
               {
                 name: '编辑',
                 action:(item)=>{
                  console.log(item)
                 },
                 icon: 'icon-edit',
                 key:'edit'
               },
               {
                 name: '删除',
                 action:()=>{
                   console.log(2)
                 },
                 icon: 'icon-delete',
                 key:"delete"
               },
             ],

     }
  },
  methods: {
      edit(){
          console.log(2)
      }

  },
}

</script>


