           <span class="hljs-attr">checkAll</span>:<span class="hljs-literal">false</span>,
           <span class="hljs-attr">checkedCities</span>:[],
           <span class="hljs-attr">templates</span>:[
           {
               <span class="hljs-attr">disabled</span>:<span class="hljs-literal">true</span>,
               <span class="hljs-attr">buttons</span>:<span class="hljs-keyword">this</span>.data,
               <span class="hljs-attr">state</span>:<span class="hljs-string">'审核不通过'</span>,
               <span class="hljs-attr">isEdit</span>:<span class="hljs-literal">true</span>,
               <span class="hljs-attr">menus</span>:[
                 {
                    <span class="hljs-attr">name</span>: <span class="hljs-string">'编辑'</span>,
                    <span class="hljs-attr">icon</span>: <span class="hljs-string">'icon-edit'</span>,
                     <span class="hljs-attr">key</span>:<span class="hljs-string">"edit"</span>
                    },
                  {
                    <span class="hljs-attr">name</span>: <span class="hljs-string">'删除'</span>,
