                     <span class="hljs-attr">key</span>:<span class="hljs-string">"delete"</span>
                  },
                      ],
               <span class="hljs-attr">label</span>:<span class="hljs-number">1234567</span>,
               <span class="hljs-attr">id</span>:<span class="hljs-number">1234567</span>,
               <span class="hljs-attr">name</span>:<span class="hljs-string">'店铺开业宣传活动'</span>,
               <span class="hljs-attr">stateColor</span>:<span class="hljs-string">"#FF404F"</span>

           },
            {
               <span class="hljs-attr">disabled</span>:<span class="hljs-literal">false</span>,
               <span class="hljs-attr">buttons</span>:<span class="hljs-keyword">this</span>.data,
               <span class="hljs-attr">state</span>:<span class="hljs-string">'审核通过'</span>,
               <span class="hljs-attr">stateColor</span>:<span class="hljs-string">"#33CE90"</span>,
