<h3>Attributes</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>disabled</td>
<td>是否禁用</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>border</td>
<td>是否出现选中边框（选中状态默认出现）</td>
<td>Boolean</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>modelType</td>
<td>选中遮罩层类型</td>
<td>String</td>
<td>primary</td>
<td>primary</td>
</tr>
<tr>
<td>buttons</td>
<td>按钮省略组件列表</td>
<td>Array</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>stateColor</td>
<td>状态圆圈颜色</td>
<td>String</td>
<td>#123456</td>
<td>-</td>
</tr>
<tr>
<td>state</td>
<td>状态</td>
<td>String</td>
<td>—</td>
<td>-</td>
</tr>
<tr>
<td>menus</td>
<td>更多操作组件列表</td>
<td>Array</td>
<td>—</td>
<td>-</td>
</tr>
<tr>
<td>label</td>
<td>选中状态的值</td>
<td>string number  boolean</td>
<td>—</td>
<td>-</td>
</tr>
<tr>
<td>isEdit</td>
<td>是否出现checkout选中框</td>
<td>boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>value / v-mode</td>
<td>绑定值</td>
<td>string number  boolean</td>
<td>—</td>
<td>-</td>
</tr>
<tr>
<td>id</td>
<td>模版id</td>
<td>string number</td>
<td>—</td>
<td>-</td>
</tr>
<tr>
<td>name</td>
<td>模版名称</td>
<td>string number</td>
<td>—</td>
<td>-</td>
</tr>
<tr>
<td>footer</td>
<td>是否显示底部状态</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
</tbody>
</table>
<h2>Template 事件</h2>
<table class="table"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>返回值</th>
</tr>
</thead>
<tbody>
<tr>
<td>其余方法</td>
<td>请去观看elemtnui文档 此次不再多说明</td>
<td>event</td>
</tr>
</tbody>
</table>
</section></template>

<script>
export default {
  data() {
    return {
        data:[{
             name: '标签一',
             color: 'primary'
           }, {
             name: '标sdsdsddgsgs签四',
             color: 'success'
           }, {
             name: '标sdsdsddgsgs签四',
             color: 'info'
           }
           ],
           isIndeterminate:false,
           checkAll:false,
           checkedCities:[],
           templates:[
           {
               disabled:true,
               buttons:this.data,
               state:'审核不通过',
               isEdit:true,
               menus:[
                 {
                    name: '编辑',
                    icon: 'icon-edit',
                     key:"edit"
                    },
                  {
                    name: '删除',
                    icon: 'icon-delete',
                     key:"delete"
                  },
                      ],
               label:1234567,
               id:1234567,
               name:'店铺开业宣传活动',
               stateColor:"#FF404F"

           },
            {
               disabled:false,
               buttons:this.data,
               state:'审核通过',
               stateColor:"#33CE90",
               isEdit:true,
               menus:[
                 {
                    name: '编辑',
                    icon: 'icon-edit',
                     key:"edit"
                    },
                  {
                    name: '删除',
                    icon: 'icon-delete',
                     key:"delete"
                  },
                   ],
               label:123456,
               id:123456,
               name:'店铺开业宣传活动'

                      }
           ],

     }
  },
  methods:{
    handleCheckAllChange(val){
             this.checkedCities = val ? this.getLabels : [];
             this.isIndeterminate = false;
    },
     handleCheckedCitiesChange(value) {

            let checkedCount = value.length;
            this.checkAll = checkedCount === this.templates.length;
            this.isIndeterminate = checkedCount > 0 && checkedCount < this.templates.length;
     },
  },
  computed:{
    getLabels(){

        return this.templates.map(item=>!item.disabled&&item.label)
       }
  }
}

</script>


